import React, { useState } from "react";
import { Helmet } from 'react-helmet';

function Room() {
    const [isPopupOpen, setIsPopupOpen] = useState(false);

    const togglePopup = () => {
        setIsPopupOpen(!isPopupOpen);
    };

    return (
        <div className="room-page">
            <Helmet>
                <title>ห้องมาตรฐาน - Directory Landing Page</title>
                <meta name="description" content="ห้องพักในโรงแรมที่มีขนาดกะทัดรัดที่สุดและเหมาะสำหรับผู้เข้าพักที่ต้องการความสะดวกสบายในราคาประหยัด" />
                <meta name="keywords" content="ห้องพัก, โรงแรม, ห้องมาตรฐาน, ที่พัก, ราคาประหยัด" />
            </Helmet>

            <div className="room-header" style={{ backgroundColor: '#ff9f00', color: '#fff', padding: '20px', textAlign: 'center' }}>
                ห้องมาตรฐาน
            </div>
            <div className="room-content">
                <div className="room-card">
                    <div className="room-image">
                        <img src="assets/img/r1.png" alt="Standard room" className="room-img" />
                    </div>
                    <div className="room-details">
                        <h1 className="room-title">ห้องมาตรฐาน</h1>
                        <p className="room-description">
                            ห้องพักในโรงแรมที่มีขนาดกะทัดรัดที่สุด โดยห้องประเภทนี้จะมีสิ่งอำนวยความสะดวกพื้นฐานที่จำเป็น เช่น เตียงนอน ห้องน้ำ ทีวี และเครื่องปรับอากาศ เหมาะสำหรับผู้เข้าพักที่เดินทางคนเดียว หรือมาเป็นคู่อีกทั้งยังสะดวกต่อการเข้าพักเพียงไม่กี่วันและต้องการห้องพักในราคาประหยัด
                        </p>
                    </div>
                    <div className="room-actions">
                        <button className="btn details-btn" onClick={togglePopup}>รายละเอียดห้องพัก</button>
                        <div className="room-icons">
                            <a href="#" className="map-icon"><i className="fa fa-map-pin"></i></a>
                            <a href="#" className="upload-icon"><i className="fa fa-upload"></i></a>
                            <a href="#" className="heart-icon"><i className="fa fa-heart"></i></a>
                        </div>
                    </div>
                </div>
            </div>

            {/* Popup Modal */}
            {isPopupOpen && (
                <div className="popup-overlay">
                    <div className="popup-content">
                        <h2>รายละเอียดห้องพัก</h2>
                        <p>
                            ห้องมาตรฐานมีขนาดกะทัดรัดและสะดวกสบาย เหมาะสำหรับผู้เข้าพักที่ต้องการความเรียบง่ายพร้อมสิ่งอำนวยความสะดวกพื้นฐาน เช่น
                            เตียงนอน ห้องน้ำ ทีวี และเครื่องปรับอากาศ
                        </p>
                        <button className="btn close-btn" onClick={togglePopup}>ปิด</button>
                    </div>
                </div>
            )}

<div className="room-header" style={{ backgroundColor: '#ff9f00', color: '#fff', padding: '20px', textAlign: 'center' }}>
                ห้องมาตรฐาน
            </div>
            <div className="room-content">
                <div className="room-card">
                    <div className="room-image">
                        <img src="assets/img/r1.png" alt="Standard room" className="room-img" />
                    </div>
                    <div className="room-details">
                        <h1 className="room-title">ห้องมาตรฐาน</h1>
                        <p className="room-description">
                            ห้องพักในโรงแรมที่มีขนาดกะทัดรัดที่สุด โดยห้องประเภทนี้จะมีสิ่งอำนวยความสะดวกพื้นฐานที่จำเป็น เช่น เตียงนอน ห้องน้ำ ทีวี และเครื่องปรับอากาศ เหมาะสำหรับผู้เข้าพักที่เดินทางคนเดียว หรือมาเป็นคู่อีกทั้งยังสะดวกต่อการเข้าพักเพียงไม่กี่วันและต้องการห้องพักในราคาประหยัด
                        </p>
                    </div>
                    <div className="room-actions">
                        <button className="btn details-btn" onClick={togglePopup}>รายละเอียดห้องพัก</button>
                        <div className="room-icons">
                            <a href="#" className="map-icon"><i className="fa fa-map-pin"></i></a>
                            <a href="#" className="upload-icon"><i className="fa fa-upload"></i></a>
                            <a href="#" className="heart-icon"><i className="fa fa-heart"></i></a>
                        </div>
                    </div>
                </div>
            </div>
            <div className="room-content">
                <div className="room-card">
                    <div className="room-image">
                        <img src="assets/img/r1.png" alt="Standard room" className="room-img" />
                    </div>
                    <div className="room-details">
                        <h1 className="room-title">ห้องมาตรฐาน</h1>
                        <p className="room-description">
                            ห้องพักในโรงแรมที่มีขนาดกะทัดรัดที่สุด โดยห้องประเภทนี้จะมีสิ่งอำนวยความสะดวกพื้นฐานที่จำเป็น เช่น เตียงนอน ห้องน้ำ ทีวี และเครื่องปรับอากาศ เหมาะสำหรับผู้เข้าพักที่เดินทางคนเดียว หรือมาเป็นคู่อีกทั้งยังสะดวกต่อการเข้าพักเพียงไม่กี่วันและต้องการห้องพักในราคาประหยัด
                        </p>
                    </div>
                    <div className="room-actions">
                        <button className="btn details-btn" onClick={togglePopup}>รายละเอียดห้องพัก</button>
                        <div className="room-icons">
                            <a href="#" className="map-icon"><i className="fa fa-map-pin"></i></a>
                            <a href="#" className="upload-icon"><i className="fa fa-upload"></i></a>
                            <a href="#" className="heart-icon"><i className="fa fa-heart"></i></a>
                        </div>
                    </div>
                </div>
            </div>

            {/* Popup Modal */}
            {isPopupOpen && (
                <div className="popup-overlay">
                    <div className="popup-content">
                        <h2>รายละเอียดห้องพัก</h2>
                        <p>
                            ห้องมาตรฐานมีขนาดกะทัดรัดและสะดวกสบาย เหมาะสำหรับผู้เข้าพักที่ต้องการความเรียบง่ายพร้อมสิ่งอำนวยความสะดวกพื้นฐาน เช่น
                            เตียงนอน ห้องน้ำ ทีวี และเครื่องปรับอากาศ
                        </p>
                        <button className="btn close-btn" onClick={togglePopup}>ปิด</button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Room;
