import React from 'react';
import './App.css';
import Navbar from './components/navbar';
import Home from './pages/Home';
import Room from './pages/Room'; // นำเข้า Room component
import Foot from './pages/Foot';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'; // นำเข้า Router, Routes, Route จาก react-router-dom

function App() {
  return (
    <div className="App">
      <Router>
        {/* Navbar จะปรากฏในทุกหน้า */}
        <Navbar />

        {/* กำหนด Routes สำหรับการเปลี่ยนหน้า */}
        <Routes>
          {/* หน้าหลัก (Home) จะใช้ URL "/" */}
          <Route path="/home" element={<Home />} />
          
          {/* หน้าห้อง (Room) จะใช้ URL "/room" */}
          <Route path="/room" element={<Room />} />
        </Routes>

        {/* Footer จะปรากฏในทุกหน้า */}
        <Foot />
      </Router>
    </div>
  );
}

export default App;
